import React, { useState } from "react";
import { Modal } from "react-bootstrap";
import { useTranslation } from "react-i18next";

const ServiceList = ({ limit = null }) => {
  const { t } = useTranslation();
  const availableService = t("availableService.items", { returnObjects: true });

  const [currentService, setCurrentService] = useState(null);
  const [isModalVisible, setIsModalVisible] = useState(false);

  // التأكد أن availableService هو مصفوفة
  if (!Array.isArray(availableService)) {
    return <p>{t("error.loadText")}</p>;
  }

  // تحديد عدد الخدمات المعروضة بناءً على limit
  const servicesToDisplay = limit
    ? availableService.slice(0, limit)
    : availableService;

  const handleServiceSelection = (service) => {
    setCurrentService(service);
    setIsModalVisible(true);
  };

  const handleCloseModal = () => {
    setIsModalVisible(false);
    setCurrentService(null);
  };

  return (
    <div className="container-fluid">
      <div className="container shadow bg-white rounded">
        <h1 className="py-4">{t("availableService.title")}</h1>
        <div className="row g-4">
          {servicesToDisplay.map((service, index) => (
            <div key={index} className="col-md-4 col-sm-6 service-item">
              <h3 className="service-title">{service.title}</h3>
              <p className="service-description">{service.description}</p>
              <button
                className="btn btn-primary"
                onClick={() => handleServiceSelection(service)}
              >
                {t("availableServicesd.items.readMore")}
              </button>
            </div>
          ))}
        </div>

        {/* المودال الذي سيظهر عند الضغط على الزر */}
        <Modal show={isModalVisible} onHide={handleCloseModal}>
          <Modal.Header closeButton>
            <Modal.Title>
              {currentService ? currentService.title : ""}
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <p>{currentService ? currentService.description : ""}</p>
            <h6>{currentService ? currentService.details : ""}</h6>
          </Modal.Body>
          <Modal.Footer>
            <button className="btn btn-secondary" onClick={handleCloseModal}>
              {t("availableServicesd.items.close")}
            </button>
          </Modal.Footer>
        </Modal>
      </div>
    </div>
  );
};

export default ServiceList;
